*, :before, :after {
  box-sizing: border-box;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  display: flex;
}

header, #game-container, #result-display {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#game-container {
  width: 100%;
  max-width: 600px;
  min-height: 500px;
}

#game-stats {
  flex-direction: row;
  display: flex;
}

.stats {
  padding: 1rem;
}

.hidden {
  display: none !important;
}
